import { Injectable } from '@angular/core';

import { Categoria } from './categoria';
import { CATEGORIAS } from './mock-categorias';

@Injectable()
export class CategoriaService {
  getCategorias(): Promise<Categoria[]> {
    return Promise.resolve(CATEGORIAS);
  }

  getCategoria(id: number): Promise<Categoria> {
    return this.getCategorias()
               .then(categorias => categorias.find(categoria => categoria.id === id));
  }

  getCategoriaByPic(pic: string): Promise<Categoria> {
    return this.getCategorias()
               .then(categorias => categorias.find(categoria => categoria.pic === pic));
  }

}


