declare var jQuery : any;
import { Component } from '@angular/core';

import { Categoria } from './categoria';
import { CategoriaService } from './categoria.service';

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.css']
})
export class FooterComponent {
  categorias: Categoria[];

  constructor(
    private categoriaService: CategoriaService
  ) { }

  getCategorias(): void {
    this.categoriaService.getCategorias().then(categorias => this.categorias = categorias)
  }

  ngOnInit(): void {
    this.getCategorias();
    jQuery('form').submit(function(e) {
      e.preventDefault();
      let data = jQuery('form').serialize();
      jQuery.ajax({
        url: "cartero.php",
        method: "POST",
        data: data,
        success: function() {
          alert('Gracias por tu mensaje, le contactaremos pronto.');
          jQuery('form').reset();
        },
        error: function() {
          alert('Ocurrió un error, intente de nuevo más tarde.');
        }
      });
      return false;
      
    });
  }
}



